@font-face {
  font-family: Avenir;
  src: url(./fonts/Avenir-Book.ttf);
  font-style: normal;
  font-weight: 300;
}

@font-face {
  font-family: Avenir;
  src: url(./fonts/Avenir-Heavy.ttf);
  font-weight: 700;
}

@font-face {
  font-family: Avenir;
  src: url(./fonts/Avenir.ttc);
}

html {
  font-family: Avenir, sans-serif;
  box-sizing: border-box;
  color: #3e4e61;
}

.container {
  margin: 20px 0;
}

.container__search {
  background-image: url(./images/Search.svg);
  background-repeat: no-repeat;
  border: 1px solid #e1e7ed;
  box-sizing: border-box;
  box-shadow: 0 1px 8px rgba(61, 78, 97, 0.1);
  border-radius: 4px;
  outline: none;
  font-weight: 300;
  font-family: inherit;
}

.big {
  width: 100%;
  height: 70px;
  font-size: 16px;
  line-height: 70px;
  padding-left: 62px;
  background-position: left 26px bottom 25px;
}

.small {
  width: 100%;
  height: 42px;
  font-size: 14px;
  line-height: 42px;
  padding-left: 33px;
  background-position: left 12px bottom 15px;
  background-size: 11px;
}

.container__search:hover {
  box-shadow: 0 3px 8px rgba(61, 78, 97, 0.2);
}

.container__search:focus {
  background: url(./images/Search.svg), linear-gradient(180deg, #fff 0%, #f6f6f7 100%);
  background-repeat: no-repeat;
  border-radius: 3px;
  line-height: 22px;
  font-weight: 700;
  color: #3d4e61;
}

.big:focus {
  background-position: 26px, 0;
}

.small:focus {
  background-position: 13px, 0;
  background-size: 11px;
}
